import Fixture from "../Models/fixture";
import axios from 'axios';
/*
 * action creators
 */
export var GET_FIXTURE_SUCCESS = 'GET_FIXTURE_SUCCESS';
function getFixtureSuccess(fixture) {
    return {
        type: GET_FIXTURE_SUCCESS,
        pending: false,
        fixture: fixture,
        error: null
    };
}
export var GET_FIXTURE_FAILURE = 'GET_FIXTURE_FAILURE';
export function getFixtureFailure(error) {
    return {
        type: GET_FIXTURE_FAILURE,
        pending: false,
        fixture: null,
        error: error
    };
}
export var GET_FIXTURE = 'GET_FIXTURE';
export function getFixture() {
    return function (dispatch) {
        dispatch({ type: GET_FIXTURE, pending: true });
        return axios.get("http://dev.webuildbots.ai:9123/fixtures?fixtureId=1234567&token=" + process.env.REACT_APP_APPLICATIONKEY)
            .then(function (response) {
            var fixture = new Fixture().deserialize(response.data);
            dispatch(getFixtureSuccess(fixture));
        })
            .catch(function (error) {
            console.error('error: ', error);
            dispatch(getFixtureFailure(error));
        });
    };
}
