import PlayerDetail from "../Models/playerInfo";
import axios from 'axios';
/*
 * action types
 */
/*
* action creators
*/
export var GET_PLAYER_DETAIL = 'GET_PLAYER_DETAIL';
export function getPlayerDetail(playerId) {
    return function (dispatch) {
        dispatch({ type: GET_PLAYER_DETAIL,
            pending: true });
        return axios.get("http://dev.webuildbots.ai:9123/players?playerId=" + playerId + "&token=" + process.env.REACT_APP_APPLICATIONKEY)
            .then(function (response) {
            var playerDetail = new PlayerDetail().deserialize(response.data);
            dispatch(getPlayerDetailSuccess(playerDetail));
        })
            .catch(function (error) {
            dispatch(getPlayerDetailFailure(error));
        });
    };
}
export var GET_PLAYER_DETAIL_SUCCESS = 'GET_PLAYER_DETAIL_SUCCESS';
export function getPlayerDetailSuccess(playerDetail) {
    return {
        type: GET_PLAYER_DETAIL_SUCCESS,
        pending: false,
        playerDetail: playerDetail,
        error: null
    };
}
export var GET_PLAYER_DETAIL_FAILURE = 'GET_PLAYER_DETAIL_FAILURE';
export function getPlayerDetailFailure(error) {
    return {
        type: GET_PLAYER_DETAIL_FAILURE,
        pending: false,
        playerDetail: null,
        error: error
    };
}
